import * as React from "react"
import { Link } from "gatsby"

const Header = ({ siteTitle }) => (
  <header class="flex flex-wrap flex-col items-center">
    <div class="flex flex-wrap justify-between">
        <div class="flex flex-wrap flex-col items-start mt-10 basis-10 md:basis-full">
            <h1 class="text-7xl tracking-tighter shrink"><span class="font-bold">THE</span> PHILOSOPHY CLUB</h1>
            <h2 class="text-3xl md:self-end">@UVA</h2>
        </div >
        <button id="hamburger-button" class = "text-6xl md:hidden cursor-pointer self-start mt-10 mr-2 -ml-24">
            &#9776;
        </button>
    </div>
    <div>
        <nav class="hidden md:block"> 
            <ul class="mt-8">
                <li class="md:space-x-32 text-2xl">
                <Link class="hover:opacity-70" to="/">Home</Link>
                <Link class="hover:opacity-70" to="/events">Events</Link>
                <Link class="hover:opacity-70" to="/archive">Archive</Link> 
                <Link class="hover:opacity-70" to="/contact">Contact</Link>
                </li>
            </ul>
        </nav>
    </div>
  </header> 
)

export default Header
